body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  background-color: gray;
  height: 100vh;
}

.bottom {
  max-width: 1920px;
  min-width: 920px;
  background-image: url(/static/media/image3.dafe13c8.png);
  height: 65vh;
  background-size: 1950px auto;
  background-position: 110% 53%;
}

.bottom__header {
  height: 40px;
  background-color: #414f6b;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.bottom__header-1 {
  font-size: 20px;
  text-decoration: underline;
  color: #414f6b;
  font-weight: 700;
  font-family: "Oswald";
  background-color: #b0b4be;
  padding: 9px;
  margin-left: 20vw;
  margin-right: 1vw;
}
.bottom__header-2 {
  font-size: 20px;
  color: #b0b4be;
  font-weight: 700;
  font-family: "Oswald";
}

.bottom__footer {
  background-color: #414f6b;
  margin-top: 25vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.bottom__footer-left {
  display: flex;
  margin-top: 10px;
}

.bottom__footer-leftText {
  font-size: 21px;
  letter-spacing: 0px;
  line-height: 23px;
  text-transform: uppercase;
  color: #63769d;
  font-family: "Bebas";
  margin-left: 10px;
}

.bottom__footer-leftText > p {
  margin-block: 0;
}

.bottom__footer-right {
  font-size: 12px;
  letter-spacing: 1px;
  line-height: 20px;
  text-transform: uppercase;
  color: #63769d;
  font-weight: 900;
  font-style: italic;
  font-family: "Lato";
  margin-right: 20vw;
}

.schedule {
  min-width: 350px;
  opacity: 0.702;
  width: 20vw;
  height: 229px;
  background-color: #ffffff;
  margin-left: 21.8vw;
  margin-top: 9vw;
  display: flex;
  flex-direction: column;
  align-items: inherit;
}

.schedule > h1 {
  font-size: 32px;
  color: #414f6b;
  font-weight: 700;
  font-family: "Oswald";
  margin: 1vw;
  align-self: flex-start;
}

.schedule > p {
  /* width: 361px; */
  font-size: 16px;
  line-height: 24px;
  color: #000000;
  font-weight: 300;
  font-family: "Lato";
  margin-block: 0;
  display: flex;
}

.schedule__span1 {
  margin-left: 1vw;
  min-width: 10vw;
  width: 110px;
}
.schedule__span2 {
  /* margin-left: 3vw; */
}

@media only screen and (max-width: 1500px) {
  .bottom {
    background-image: url(/static/media/image3.dafe13c8.png);
    height: 65vh;
    background-size: 1400px auto;
    background-position: 70% 53%;
  }
}

.mid {
  max-width: 1920px;
  min-width: 920px;
  background-image: url(/static/media/image2.64b54bf8.png);
  /* height: 100vh; */
  background-size: 2000px auto;
  background-position: 80% 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.mid__left {
  display: flex;
  margin-left: 20vw;
}

.mid__left > h1 {
  opacity: 0.502;
  font-size: 136px;
  color: #414f6b;
  font-weight: 700;
  font-family: "Oswald";
}

.mid__left > h5 {
  font-size: 32px;
  color: #414f6b;
  font-weight: 700;
  font-family: "Oswald";
  margin-top: 14vh;
  margin-left: -1vw;
}

.mid__body > p {
  width: 50vw;
  height: 339px;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
  font-weight: 300;
  font-family: "Lato";
  margin-left: 24vw;
  margin-top: -11vh;
}

.mid__buttom {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 15vh;
}

.carousel {
  max-width: 1920px;
  min-width: 920px;
  background-color: #414f6b;
  opacity: 0.9;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 15vh;
  padding-bottom: 1vh;
}

.carousel__images > img {
  width: 10vw;
  padding: 10px;
}

.dot {
  display: flex;
}

.dot > span {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #232c3ce1;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active {
  background-color: white !important;
  cursor: pointer;
}
.dot > span:hover {
  background-color: white !important;
  cursor: pointer;
}

.mid__footer {
  max-width: 1920px;
  min-width: 920px;
  background-color: white;
  width: 100vw;
  height: 20vh;
  display: flex;
  /* padding: 0; */
  /* justify-content: center; */
  align-items: center;
}

.mid__footer-left {
  display: flex;
  margin-left: 20vw;
}

.mid__footer-left > h1 {
  opacity: 0.502;
  font-size: 136px;
  color: #414f6b;
  font-weight: 700;
  font-family: "Oswald";
}

.mid__footer-left > h5 {
  font-size: 32px;
  color: #414f6b;
  font-weight: 700;
  font-family: "Oswald";
  margin-top: 14vh;
  margin-left: -1vw;
}

.mid__footer-body > p {
  width: 50vw;
  height: 339px;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
  font-weight: 300;
  font-family: "Lato";
  margin-left: 3vw;
  margin-top: 24vh;
}

